const NewCourseData: CourseType = CoursesData[0];

function EmbedYoutube(props: { url: string | undefined }) {
    return (<iframe
        src={"https://www.youtube.com/embed/vPL46ZdAZrA"}
        title="YouTube video player"
        frameBorder="0"
        className='article-embed-youtube-video'
        allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
        allowFullScreen></iframe>)
}

function EmbedCover(props: { url: string }) {
    return (
        <img className='embed-cover-photo' src={props?.url} alt="" />
    )
}

function BlogPost(props: object) {
    const data = exampleData;
    return (
        <div>
            <div className="blog-content-screen-container">
                <div className='blog-content-screen-container'>
                    <div className='container-fluid post-and-side-container-fluid'>
                        <div className='container post-and-side-container'>
                            <div className='row post-and-side-row'>
                                <div className='col-lg-8 col-12 post-container'>
                                    {/* {
                                        data?.youtubeEmbedUrl
                                            ? (<EmbedYoutube url={data?.youtubeEmbedUrl} />)
                                            : (<EmbedCover url={data?.cover} />)
                                    } */}
                                    {
                                        Math.random() >= 0.5
                                            ? (<EmbedYoutube url={data?.youtubeEmbedUrl} />)
                                            : (<EmbedCover url={data?.cover} />)
                                    }
                                    <div className='category-container'>
                                        {data?.category}
                                    </div>
                                    <div className="title-container">
                                        {data?.title}
                                    </div>
                                    <div className="subtitle-container">
                                        {data?.subtitle}
                                    </div>
                                    <hr />
                                    <div className="date-container">
                                        {data?.createdAt}  &nbsp; &nbsp;|&nbsp;&nbsp;&nbsp;&nbsp;
                                        {data?.updatedAt}
                                    </div>
                                    <hr />
                                    <article dangerouslySetInnerHTML={{
                                        __html: data?.htmlContent
                                    }}>

                                    </article>
                                </div>
                                <div className='col-lg-4 col-12 side-container'>
                                    <a className='side-container-education-promotion-container' target="_blank" href={CouponAndReferralGenerator(NewCourseData)}>
                                        <div className='side-container-education-promotion'>
                                            <img className="side-container-education-promotion-cover" src={NewCourseData?.promotionCover || ""} />
                                            <h3>En Son Eğitimim</h3>
                                            <div className="side-container-education-promotion-icon">
                                                <BsFillArrowRightCircleFill size={35} color="black" />
                                            </div>
                                        </div>
                                    </a>
                                    {(data?.suggestedPosts || []).map((post, index) => {
                                        return (
                                            <Link to={{ pathname: `${post.link}` }} key={index}>
                                                <div className="post-card">
                                                    <div className='image-container'>
                                                        <img src={post.cover} width="100%" height="auto" alt={post?.title} />
                                                    </div>
                                                    <div className='title-container'>
                                                        <h4>{post?.title} </h4>
                                                    </div>
                                                </div>
                                            </Link>
                                        )
                                    })}
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <NewCourse />
        </div>
    )
}

export default BlogPost;